import React, {Component} from 'react';
import axios from 'axios';
// import Button, {LoadingButton} from '../UI/Button';
export default class CourseEditView extends Component {
    constructor(props){
        super(props);
        if (this._isNEW()) {
            this.state={
            course: {}
            };
        }
        else {
            let {course} = props.location;
            // let {course} =props.match;
            this.state={course};
        }
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    _isNEW() {
        const {id} = this.props.match.params;
        return id==='NEW';
    }

    handleInputChange(event) {
        const {name: fieldName,value} = event.target;
        this.setState({
            course: {
                ...this.state.course,
                [fieldName]: value,
            },
        });
    }
    
    handleSubmit(event) {
        event.preventDefault();
        this.setState({isSaving:true});
        const {course} = this.state;
        setTimeout(()=>{
            this.setState({isSaving:false});
            alert('Detail Saved');
        },2000);
    }

    componentDidMount() {

        const {Id} =this.props.match.params
        if ('NEW'=== Id) {
            this.setState({course:{}, isEditing: true})
        return;
        }
    // this.loadCourses()

}

    render() {
        // const {Name, CourseCode} = this.state.course;

      return (
        <div className='course-edit'>
            <div className='thumbnail'>
                <table border='1'>
                    <tr>
                        <th>Course Code</th>
                        <th>Course Name</th>
                    </tr>
                    <tr>
                        {/* <td>{CourseCode}</td> */}
                        {/* <td>{Name}</td> */}
                    </tr>
                    <tr>
                        <th>Credit</th>
                        <th>Max Number</th>
                    </tr>
                    <tr>
                        <td>24</td>
                        <td>90</td>
                    </tr>
                    <tr>
                        <th colSpan="2">Description</th>
                    </tr>
                    <td colSpan="2">Math is science.</td>
                </table>
            </div>
        </div>
      )
    }
}


